// src/utils/colorMaps.js
// ---------------------------------------------------
//  ,  , normalizeGrade  
// ---------------------------------------------------

export const gradeColors = {
  NORMAL: '#9e9e9e',
  MAGIC: '#5c6bc0',
  RARE: '#26a69a',
  UNIQUE: '#7cb342',
  EPIC: '#ab47bc',
  LEGENDARY: '#ff9800',
  DIVINE: '#00acc1',
  MYSTIC: '#c62828',
  ETERNAL: '#455a64',
  INFINITY: '#ffca1a',
  ELDER: '#1e88e5',
  AKASHIC: '#8e24aa',
  EMPYREAN: '#3949ab',
};
export const elementColors = {
  FIRE: '#ef5350',
  WATER: '#42a5f5',
  GRASS: '#66bb6a',
  LIGHT: '#ffd54f',
  DARK: '#8d6e63',
  NEUTRAL: '#bdbdbd',
};

// /  
export function normalizeGrade(raw) {
  if (!raw) return '';

  const str = String(raw).trim().toUpperCase();

  //   
  if (gradeColors[str]) return str;

  //     (  )
  if (str.includes('')) return 'NORMAL';
  if (str.includes('')) return 'NORMAL';
  if (str.includes('')) return 'MAGIC';
  if (str.includes('')) return 'RARE';
  if (str.includes('')) return 'RARE';
  if (str.includes('')) return 'UNIQUE';
  if (str.includes('')) return 'UNIQUE';

  if (str.includes('')) return 'EPIC';
  if (str.includes('')) return 'LEGENDARY';

  if (str.includes('')) return 'LEGENDARY';
  if (str.includes('')) return 'DIVINE';
  if (str.includes('')) return 'MYSTIC';
  if (str.includes('')) return 'ETERNAL';
  if (str.includes('')) return 'INFINITY';
  if (str.includes('')) return 'ELDER';
  if (str.includes('')) return 'AKASHIC';
  if (str.includes('')) return 'EMPYREAN';
  if (str.includes('')) return 'EMPYREAN';

  return str;
}

//      
export function normalizeElement(raw) {
  if (!raw) return '';
  const str = String(raw).trim().toUpperCase();

  if (elementColors[str]) return str; // Already English

  if (str.includes('')) return 'FIRE';
  if (str.includes('')) return 'WATER';
  if (str.includes('')) return 'GRASS';
  if (str.includes('')) return 'LIGHT';
  if (str.includes('')) return 'DARK';
  return 'NEUTRAL';
}

export function colorOfGrade(grade) {
  const g = normalizeGrade(grade);
  return gradeColors[g] ?? '#bdbdbd';
}

export function colorOfElement(el) {
  const e = normalizeElement(el);
  return elementColors[e] ?? '#bdbdbd';
}