// src/components/Step3OwnedStats.jsx
import React, { useMemo } from 'react';
import EntityCard from './EntityCard';
import GradeSelectBar from './GradeSelectBar';
import { normalizeGrade } from '../utils/colorMaps';

export default function Step3OwnedStats({ skillData, ownedSkills, setOwnedSkills, calculateResult, goPrev }) {
  const ownedKeys = useMemo(() => new Set(ownedSkills.map((s) => s.id ?? s.name)), [ownedSkills]);

  const { activeGrades, isAllSelected } = useMemo(() => {
    const skillsByGrade = skillData.reduce((acc, sk) => {
      const grade = normalizeGrade(sk.grade);
      if (!acc[grade]) acc[grade] = [];
      acc[grade].push(sk);
      return acc;
    }, {});

    const activeGrades = new Set();
    for (const grade in skillsByGrade) {
      const allInGradeSelected = skillsByGrade[grade].every(sk => ownedKeys.has(sk.id ?? sk.name));
      if (allInGradeSelected) {
        activeGrades.add(grade);
      }
    }

    const isAllSelected = skillData.length > 0 && ownedSkills.length === skillData.length;

    return { activeGrades, isAllSelected };
  }, [ownedSkills, skillData, ownedKeys]);

  const toggleOwned = (sk) => {
    const key = sk.id ?? sk.name;
    setOwnedSkills((prev) =>
      ownedKeys.has(key) ? prev.filter((x) => (x.id ?? x.name) !== key) : [...prev, sk]
    );
  };

  const onSelectByGradeToggle = (gradeStd, kind, add) => {
    if (kind !== 'skill') return;
    setOwnedSkills((prev) => {
      const sameGrade = skillData.filter((s) => normalizeGrade(s.grade) === gradeStd);
      if (add) {
        const ids = new Set(prev.map((s) => s.id ?? s.name));
        const merged = [...prev, ...sameGrade.filter((s) => !ids.has(s.id ?? s.name))];
        return merged;
      } else {
        return prev.filter((s) => normalizeGrade(s.grade) !== gradeStd);
      }
    });
  };

  const onSelectAll = (kind, allGrades) => {
    if (kind !== 'skill') return;
    setOwnedSkills((prev) => {
      const ids = new Set(prev.map((s) => s.id ?? s.name));
      const toAdd = skillData.filter(
        (s) => allGrades.includes(normalizeGrade(s.grade)) && !ids.has(s.id ?? s.name)
      );
      return [...prev, ...toAdd];
    });
  };

  const onClearAll = () => setOwnedSkills([]);

  return (
    <div className="p-4 flex flex-col min-h-screen h-screen">
      <h2 className="text-center font-bold text-xl sm:text-2xl">STEP 3   </h2>

      <GradeSelectBar
        title="  "
        kind="skill"
        activeGrades={activeGrades}
        isAllSelected={isAllSelected}
        onSelectByGradeToggle={onSelectByGradeToggle}
        onClearAll={onClearAll}
        onSelectAll={onSelectAll}
      />

      <div className="flex-1 overflow-y-auto p-2 sm:p-3 border border-gray-200 rounded-lg bg-white mt-3 shadow-inner max-h-[calc(100vh-250px)]">
        <div className="grid grid-cols-3 sm:grid-cols-3 md:grid-cols-4 lg:grid-cols-5 xl:grid-cols-6 gap-1 sm:gap-4">
          {skillData.map((sk, i) => (
            <EntityCard
              key={`${sk.name}-${i}`}
              type="skill"
              data={sk}
              selected={ownedKeys.has(sk.id ?? sk.name)}
              onToggle={() => toggleOwned(sk)}
            />
          ))}
        </div>
      </div>

      <div className="text-center mt-4 flex justify-center gap-4">
        <button
          onClick={goPrev}
          className="bg-gray-500 text-white border-none px-4 py-2 rounded-lg cursor-pointer font-semibold"
        >
            
        </button>
        <button
          onClick={calculateResult}
          className="bg-green-600 text-white border-none px-4 py-2 rounded-lg cursor-pointer font-semibold"
        >
            (DPS )
        </button>
      </div>
    </div>
  );
}
